<template>
<div style="display:inline-block">
    <v-row>
      <v-col cols="12">
       <!-- <base-material-card color="success" inline title="续费编辑" class="px-4 py-2" style>
           <v-subheader class="display-1 mt-3">
            Text fields
          </v-subheader>-->
           <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              class="mr-2"
              v-on="on"
              v-bind="attrs"
             @click="start(renewalId)"
            >mdi-pencil</v-icon>
          </template>
          <span>编辑</span>
        </v-tooltip>
       <v-dialog v-model="dialog" persistent max-width="700px">
          <!-- <base-material-card color="success" inline title="续费" class="px-4 py-2" style> -->
          <v-card>
            <v-card-title>
              <span class="headline">编辑续费</span>
            </v-card-title>
            <v-card-text>
          <v-form class="pt-1" v-model="valid" :lazy-validation="lazy">
            <v-row align="center" class="ml-1">
              <v-text-field
                :rules="nameRules"
                required
                label="续约天数(*必填)"
                v-model="renewalObj.renewalDays"
                outlined
                class="ml-2"
                cols="12"
                sm="6"
              />
            </v-row>

            <v-row align="center" class="ml-1">
              <v-text-field
                :rules="nameRules"
                required
                label="版本(*必填)"
                v-model="renewalObj.version"
                outlined
                class="ml-2"
                cols="12"
                sm="6"
              />
            </v-row>

            <v-row align="center" class="ml-1">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                :return-value.sync="date"
                min-width="200px"
                offset-y
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="date"
                    :rules="nameRules"
                    color="red"
                    label="开始续约时间"
                    readonly
                    v-on="on"
                    outlined
                    class="ml-2"
                    cols="12"
                    sm="6"
                  />
                </template>

                <v-date-picker v-model="date" color="secondary" scrollable locale="zh-cn">
                  <v-spacer />
                  <v-btn color="secondary" large @click="menu = false">Cancel</v-btn>

                  <v-btn color="secondary" large @click="$refs.menu.save(date)">OK</v-btn>
                </v-date-picker>
              </v-menu>
            </v-row>
          </v-form>
          <v-row align="center" class="ml-1">
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="doCancel()">关闭</v-btn>
            <v-btn color="blue darken-1" :disabled="!valid" text @click="doSave(renewalId)">保存</v-btn>
          </v-row>
            </v-card-text>
          </v-card>
       </v-dialog>
        <!-- </base-material-card> -->
      </v-col>
    </v-row>
</div>
</template>

<script>
export default {
  // created() {
  //   let getData = {id:this.renewalId};
  //   this.$http
  //     .get("/user/api/v1/record/detailRenewal",getData)
  //     .then(res => {
  //       this.renewalObj = res.data;
  //       this.date = res.data.renewalTime;
  //     });
  // },

  name: "editRenewalInfo",

  data: () => ({
    dialog: false,
    date: "",
    menu: false,
    renewalObj: {
      id: "",
      companyUniqueCode: "",
      renewalDays: "",
      renewalTime: "",
      version: ""
    },

    lazy: false,
    valid: true,
    //表单验证
    nameRules: [v => !!v || "该选项为必填字段"]
  }),

  props: {
    renewalId: {
      type: Number
    },
    companyUniqueCode:{
      type: String
    }
  },

  methods: {
    start(renewalId) {
      if(renewalId){
       this.dialog = true
        let getData = {id:renewalId};
       this.$http
      .get("/user/api/v1/record/detailRenewal",getData)
      .then(res => {
        this.renewalObj = res.data;
        this.date = res.data.renewalTime;
      });
      }

    },
    doSave(renewalId) {
      let postData = {
        id:renewalId,
        companyUniqueCode: this.companyUniqueCode,
        renewalDays: this.renewalObj.renewalDays,
        renewalTime: this.renewalObj.renewalTime,
        version: this.renewalObj.version
      };
      this.$http
        .post("/user/api/v1/record/updateRenewal", postData)
        .then(response => {
          if (response.success) {
            response.msg = "续约更新成功";
            this.$store.commit("SET_SNACK", response);
            this.$emit("editRenewal",this.companyUniqueCode)
          } else {
            this.$store.commit("SET_SNACK", response);
          }
        })
        .catch(error => {
          console.log(error);
        });

        this.dialog = false;
    },
    doCancel() {
      this.dialog = false;
    }
  }
};
</script>
